@tailwind base;
@tailwind components;
@tailwind utilities;

.controler {
  @apply flex items-center gap-2;
}

.device {
  @apply w-16 h-16 flex items-center justify-center text-xl cursor-pointer text-lightGray shadow-2xl  rounded-lg;
}
#slider input {
  -webkit-appearance: none;
}
#slider input::-webkit-slider-thumb {
  -webkit-appearance: none;
  background-color: #fbfbfb;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  border: 8px solid #7b43fe;
  cursor: pointer;
}

#slider progress {
  -webkit-appearance: none;
}
progress[value]::-webkit-progress-bar {
  border-radius: 9999px;
  /* height: 12px; */
  background-color: #f4f3f9;
}
progress[value]::-webkit-progress-value {
  border-radius: 9999px;
  background-color: #d0bbfe;
}
